import http from '@/components/utils/http.js';
import { mapState, mapMutations } from 'vuex';

export default {
	data() {
		return {
			serverapi: this.apiServer,
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			goodsList: [],
			goodsList2: []
			
		};
	},

	onLoad() {
		this.loadData();
	},

	methods: {
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		async loadData() {
			
			this.getbanner();
			this.getproductlist();
		},
		
		
		getproductlist(){
		let opts = {
			url: '/productApi/getgradeproduct/',
			method: 'post'
		};
		
		let param = { };
		http.httpTokenRequest(opts, param).then(
			res => {
				//打印请求返回的数据
				if (res.data['code'] == 0) {
					let goodsList1 = res.data['goodsList1'];
					let goodsList2 = res.data['goodsList2'];
					this.goodsList = goodsList1 || [];
					this.goodsList2 = goodsList2 || [];
				} else {
					uni.showToast({ title: res.data.msg, icon: 'none' });
				}
			},
			error => {
				console.log(error);
			}
		);	
		},
		
		getbanner(){
		let	_that=this;
		let opts = {
			url: '/base/getbannerlist/',
			method: 'post'
		};
		
		let param = { };
		http.httpTokenRequest(opts, param).then(
			res => {
				//打印请求返回的数据
				if (res.data['code'] == 0) {
					let carouselList = res.data['bannerlist'];
					_that.titleNViewBackground = carouselList[0].background;
					_that.swiperLength = carouselList.length;
					_that.carouselList = carouselList;
			
				} else {
					uni.showToast({ title: res.data.msg, icon: 'none' });
				}
			},
			error => {
				console.log(error);
			}
		);	
		},

		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].background;
		},
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.psid;
			uni.navigateTo({
				url: `/pages/product/product?psid=${id}`
			});
		}
	}
};
